<div class="hello"><div class="ui segment container"><h1 class="ui header">自學<sub class="header">正式名稱為「非學校型態實驗教育」</sub></h1><section><h2 class="ui header">1. 一些關於自學生的刻板印象🥸</h2><div class="ui list"><div class="item">1.1 都是天才??  ➡️ 不一定!</div><div class="item">1.2 家裡很有錢??  ➡️ 不一定!</div><div class="item">1.3 沒有朋友??    ➡️ 完全不是!</div><div class="item">1.4 社交能力差??    ➡️ 因人而異!</div><div class="item">1.5 自制力很強??   ➡️ 因人而異!</div></div></section><section><h2 class="ui header">2. 自學申請流程🛝</h2><div class="ui list"><div class="item"><div class="header">2.1 申請時間</div><div class="list"><div class="item">2.1.1 每年四月和十月<div class="description"> <a href="https://law.moj.gov.tw/LawClass/LawAll.aspx?pcode=H0070059">	高級中等以下教育階段非學校型態實驗教育實施條例</a>第六條：前條申請人應填具申請書，並檢附實驗教育計畫，至遲於每年<span class="bold text-underline">四月三十日</span>或<span class="bold text-underline">十月三十一日</span>前提出申請。</div></div></div></div><div class="item"><div class="header">2.2 申請材料</div><div class="list"><div class="item">2.2.1 申請書 ➡️ 參考<a href="https://www.homeschool.tw/p/blog-page.html">一週完成自學申請書</a></div><div class="item">2.2.2 實驗教育計畫書 ➡️ 參考<a href="https://youtu.be/SUoT5P8_UOI?si=zdHxoRdRoCadmYMc">談學習計畫-朱佳仁主講</a></div><!-- .item 2.2.2 成績報告--><!-- .item 2.2.3 課程計劃--></div></div><!-- .item.header 2.3 面試
.list
  .item 2.3.1 帶家長面試
  .item 2.3.2 面試問題--><div class="item"><div class="header">2.3 申請人</div><div class="list"><div class="item">2.3.1 國小、國中、高中職，由法定代理人提出申請。因此，家長是申請人。想要申請自學的話，需要先和家長達成共識。</div></div></div><div class="item"><div class="header">2.4 審核與補件</div><div class="list"><div class="item">2.4.1 各縣市教育局審核</div><div class="item">2.4.2 初審不通過 ➡️ 可申請補件</div><div class="item">2.4.3 初審通過 ➡️ 受到申請成功通知</div></div></div><div class="item"><div class="header">2.5 延伸閱讀</div><div class="list"><div class="item">2.5.1 <router-link to="/senior">「第一次申請高中自學就上手」工作坊記錄</router-link></div></div></div></div></section><section><h2 class="ui header">3. 自學的種類👨‍👩‍👧‍👦</h2><div class="ui list"><div class="item"><div class="header">3.1 個人自學</div><div class="list"><div class="item">3.1.1 與學校合作：掛籍在學校，可完全在家自學，也可部分時間在學校上課，部分時間自學</div><div class="item">3.1.2 教育部掛籍：在教育部註冊，完全在家自學</div></div></div><div class="item"><div class="header">3.2 團體自學</div><div class="list"><div class="item">3.2.1 自學生團體：多個家庭或學生組成學習小組</div><div class="item">3.2.2 分攤學費：共同聘請老師或分享資源，降低成本</div></div></div><div class="item"><div class="header">3.3 機構自學</div><div class="list"><div class="item">由專業教育機構提供課程和資源，計畫書需經過審核</div></div></div></div></section><section><h2 class="ui header">4. 自學生活🧞‍♂️</h2><div class="ui list"><div class="item"><div class="header">4.1 成績評定</div><div class="list"><div class="item">4.1.1 成果報告 ➡️ 每學年結束後兩個月內(即每年九月底前)</div><div class="item">4.1.2 評分標準 ➡️ 依自學計畫</div></div></div><div class="item"><div class="header">4.2 自學課程🐙</div><div class="list"><div class="item">4.2.1 線上課程：如MOOCs、Khan Academy等平台</div><div class="item">4.2.2 實體課程：參加工作坊、講座或社區大學課程、到大學旁聽課程，也可自行設計課程</div></div></div><div class="item"><div class="header">4.3 自學資源🌵</div><div class="list"><div class="item">4.3.1 政府補助<div class="description">目前台灣高中職階段有政府補助</div></div><div class="item">4.3.2 <router-link to="/outer">線上資源：包括免費教材、教學影片和學習社群</router-link></div></div></div><div class="item"><div class="header">4.4 社交活動🤽‍♀️</div><div class="list"><div class="item">4.4.1 自學生活動：聚會、讀書會或主題討論</div><div class="item">4.4.2 外部講座與活動：參與各種公開活動，拓展視野</div><div class="item">4.4.3 競賽：參加各種競賽，如科學展覽、數學競賽等</div></div></div></div></section><section><h2 class="ui header">5. 自學的挑戰🧨</h2><div class="ui list"><div class="item"><div class="header">5.1 撞牆期</div><div class="list"><div class="item">5.1.1 不知道如何規畫：如果不知道自學的目標，可以參考<a href="https://www.youtube.com/watch?v=SUoT5P8_UOI&amp;list=PLebzuoh5ZI3JKOarYED1ASt2kG9g5sKFj" target="_blank" rel="noopener noreferrer">談學習計畫-朱佳仁主講</a></div><div class="item">5.1.2 不知道未來：如果不知道自學的未來，可以參考<a href="https://www.youtube.com/watch?v=ZPzqw3x05-g&amp;list=PLebzuoh5ZI3JKOarYED1ASt2kG9g5sKFj&amp;index=6" target="_blank" rel="noopener noreferrer">如何成為未來職場需要的自由人才-唐鳳主講</a></div></div></div><div class="item"><div class="header">5.2 家長的擔憂👀</div><div class="list"><div class="item">5.2.1 不了解自學：可請家長參考<div class="description"> <a href="https://www.alearn.org.tw/">自主學習促進會</a></div></div><div class="item">5.2.2 擔心社交問題：可請家長參考<div class="description"> <a href="https://we.alearn.org.tw">自學2.0</a></div></div></div></div></div></section><section><h2 class="ui header">6. 自學的溝通🗣️</h2><div class="ui list"><div class="item"><div class="header">6.1 與家長溝通</div><div class="list"><div class="item">計劃與目標：計劃書的撰寫、家長的期望和孩子的興趣</div></div></div><div class="item"><div class="header">6.2 與學校溝通</div><div class="list"><div class="item">合作方式：掛籍在學校，可完全在家自學，也可部分時間在學校上課，部分時間自學</div></div></div></div></section><section><h2 class="ui header">7. 自學後的發展🎯</h2><div class="ui list"><div class="item"><div class="header">7.1 畢業後的選擇</div><div class="list"><div class="item">7.1.1 考大學</div><div class="item">7.1.2 工作或實習</div></div></div><div class="item"><div class="header">7.2 自學經驗的影響</div><div class="list"><div class="item">7.2.1 成熟度 ➡️ <router-link to="four">學習自律四階段🐫</router-link></div><div class="item">7.2.2 自我管理能力</div></div></div></div></section><section><h2 class="ui header">8. 問答集❤️‍🩹</h2><p>請參考 ➡️ <router-link to="faq">自學問答集</router-link></p><div class="ui divider"> </div><h3>找夥伴👫</h3><p>請參考 ➡️ <a href="https://we.alearn.org.tw">自學2.0</a></p><div class="ui divider"> </div><h3>真人諮詢👩‍🏫</h3><p>請用「您居住的縣市」 + 「非學校」當關鍵字，即可查詢到教育局的相關業務單位，通常都會有聯絡電話。</p><div class="ui divider"> </div><h3>相關連結🪱</h3><div class="ui vertical large buttons"><a class="ui teal button" href="https://law.moj.gov.tw/LawClass/LawAll.aspx?pcode=H0070059">	高級中等以下教育階段非學校型態實驗教育實施條例</a><!-- a.ui.green.button(href="https://we.alearn.org.tw") 自學2.0--><a class="ui green button" href="https://www.alearn.org.tw">自主學習促進會</a><a class="ui blue button" href="https://galacticproject.notion.site/d7477db76ecd4ff3b453a39ba21d97ef">星河計畫：自學生經驗匯流</a></div></section></div></div>